
.points{
    @import '../variables/variables';
    @import '../components/media';
    @import '../components/colors';
    @import '../components/fonts';
    @include marginSection();
    .container{
        padding-top: 4rem;
    }
    .column-info{
        .points-header{
            display: flex;
            align-items: flex-end;
            margin-bottom: 20px;
            h1,span{
                color: $black-dark;
            }
            span{
                margin-left: 10px;
                font-size: 14px;
                opacity: .8;
            }
            h1{
                font-size: 24px;
                font-weight: 600;
                font-family: $klavika;
                color: $black-dark;
                margin-bottom: 0;

                @include media(sm){
                  font-size: 22px;
                }
                @include media(xs){
                  font-size: 20px;
                }
            }
        }
        .search__container{
            .search{
                width: 100%;
                height: 40px;
                background-color: $white-header;
                display: flex;
                align-items: center;
                justify-content: flex-start;
                border-radius: 4px;
                padding: 5px 16px;
                height: 50px;
                margin-bottom: 20px;
                input{
                    font-size: 1rem;
                    border: 0;
                    outline: none;
                    background: transparent;
                    color: $black-dark;
                    width: 100%;
                    height: 100%;
                    &::placeholder{
                        color: $black-dark;
                    }
                }
            }
            .geolocation{
            
                .geolocation__buttons{
                    display: grid;
                    grid-template-columns: repeat(2, 1fr);
                    gap: 20px;
                    button{
                        width: 100%;
                    }
                    .btn-red-transparent-icon {
                        min-width: auto;
                    }
                }
        
            }
        }
    }
    .map-container{
        min-height: 80dvh;
        height: 100%;
    
    }
}